/**
 * Service to handle the session of the user
 * Here we will manage the session timeout,
 *   - Notice the user that the session is about to end
 *   - Detect when the user still there thanks to its interactions (mouse and keyboard events)
 *   - When the session finalizes indicate it
 */
bizagi.workportal.services.sessionTimeOut = function (dataService, globalHandlersService) {
  /**
                                                                                            * Is this feature enabled?
                                                                                            * Is possible that this feature is not enabled so this flag will indicate if there is or not
                                                                                            */
  var isSessionTimeoutEnabled;

  /**
                                * How long the session longs,
                                * expressed on milliseconds
                                */
  var sessionTime;

  /**
                    * Expressed on milliseconds.
                    *
                    * After we close the session we warn to the user that its session is going to end
                    * with a modal, asking him if we wants to stay and extend the session.
                    *
                    * This will indicate when there is X time left the warn should be executed.
                    *
                    * @example 300000 means that when left 5 minutes to close the session the warn will be triggered,
                    *          30000 means that when left 30 seconds to close the session the warn will be triggered
                    */
  var alertTime;

  /**
                  * Will contain the id of the setTimeout executed to
                  * determine how much time left on the session
                  *
                  * This represent the Warning
                  */
  var countDownIDWarn = null;

  /**
                               * This represent the time to show the session expired dialog
                               */
  var countDownIDExpired = null;

  /**
                                  * Will execute immediately
                                  * Works like a constructor
                                  */
  (function constructor() {
    // Get the session time out from the config
    sessionTime = bizagiConfig.sessionTimeout * 60 * 1000;

    // If is not defined the default alarm time is 45 seconds
    alertTime = 45 * 600;

    // Determine if the session timeout is enabled or not
    isSessionTimeoutEnabled = sessionTime ? true : false;

    // If the session time out feature is enabled
    if (isSessionTimeoutEnabled) {
      // Start the countDown
      startCountDown();
    }
  })();

  /**
         * Indicate to the server that we are alive.
         *
         * @returns A promise that will resolve the call to the server to indicate that we are alive
         */
  function keepAlive() {
    var d = $.Deferred();
    $.when(
    dataService.ping()).
    done(function (data) {
      d.resolve(data);
    });
    return d.promise();
  }

  /**
     * Start the count down,
     * There are two timeout:
     *   - First one when there is X time left to close the session. Will execute the dialog to warn him and give the possibility to extend the session
     *   - Second one with the time remaining and if is completed the session will destroyed
     *
     * @param deferred
     *        Means that we need to deffer the count down because is not time yet to expire the session.
     *        Another tab takes the lead.
     */
  function startCountDown(deferred) {
    // We need to stop the current one if there is one
    stopTimeOut();

    if (deferred) {
      // Start a new container immediately after shown the warn to destroy the session.
      countDownIDExpired = setTimeout(function () {
        sessionExpired();
      }, deferred);
    } else {
      // First we will set a timeout to execute the warn, once execute the warn the put the remaining
      // time and execute the destroy session
      countDownIDWarn = setTimeout(function () {
        // Show warn dialog about the session is gonna be destroyed
        showWarn();

        // Start a new container immediately after shown the warn to destroy the session.
        countDownIDExpired = setTimeout(function () {
          sessionExpired();
        }, alertTime);

      }, sessionTime - alertTime);

      bizagi.cookie('syncTimerTimeout', Date.now());
    }
  }

  /**
     * Stop the count Downs, every of them if exits
     */
  function stopTimeOut() {
    if (countDownIDWarn) {
      clearTimeout(countDownIDWarn);
    }

    if (countDownIDExpired) {
      clearTimeout(countDownIDExpired);
    }
  }

  /**
     * validate if the user is in maintenance window now
     */
  function maintenanceWindowIsActive() {
    return bizagiConfig.maintenanceWindows.hasStartedCall() === true;
  }

  /**
     * Display a dialog to indicate if the user wants to keep the session
     */
  function showWarn() {
    if (!maintenanceWindowIsActive()) {
      globalHandlersService.publish('showDialogWidget', {
        widgetName: bizagi.workportal.widgets.widget.BIZAGI_WORKPORTAL_WIDGET_SESSION_TIMEOUT,
        closeVisible: false,
        maximize: false,
        closeOnEscape: false,
        modalParameters: {
          title: bizagi.localization.getResource('workportal-sessiontimeout-warn-title'),
          width: 600,
          height: 310 } });


    }
    stopTimeOut();
  }

  /**
     * Logic begin when the session expires.
     * The multi tab scenario is covered.
     *
     * The multi tab sync is done using a cookie
     */
  function sessionExpired() {
    /**
                              * The multi tab sync is done with that because
                              * internet explorer have problems to sync with multi tab the sessionStorage and
                              * Local storage is a know issue since 2014.
                              * With cookies that problem doesn't exits
                              */
    var syncTabCookie = bizagi.cookie('syncTimerTimeout');

    // If the cookie exits
    if (syncTabCookie) {
      /**
                         * If the time container on the cookie plus the session time is greater than the actual date
                         * means that there is another tab with newer actions, that tab will lead the expiration time,
                         * so we need to defer the expiration date to the same time of that tab.
                         *
                         * Otherwise means that I the tab with the lead, so show the session expired session.
                         */
      if (parseInt(syncTabCookie) + sessionTime > Date.now()) {
        // Sync the expiration date at the same time of the other tab
        var value = parseInt(syncTabCookie) + sessionTime;
        var deferred = value - Date.now();
        // Restart the countdown to be align with the other tab
        startCountDown(deferred);
      } else
      {
        showSessionExpired();
      }
    } else
    {
      showSessionExpired();
    }


  }

  /**
     * Just show the modal of session expired
     *
     * Close the dialog of the warning and show the session expired modal.
     */
  function showSessionExpired() {
    if (!maintenanceWindowIsActive()) {
      globalHandlersService.publish('closeCurrentDialog');

      globalHandlersService.publish('showDialogWidget', {
        widgetName: bizagi.workportal.widgets.widget.BIZAGI_WORKPORTAL_WIDGET_SESSION_TIMEOUT_EXPIRED,
        closeVisible: false,
        maximize: false,
        closeOnEscape: false,
        modalParameters: {
          title: bizagi.localization.getResource('workportal-sessiontimeout-expired-title'),
          width: 500,
          height: 310 } });


    }

    stopTimeOut();

    // When the user is on a form and the session expires, when he clicks "log in" again, the alert of
    // Are you sure of leave the site?. With this line of code we prevent that.
    $(document).data('auto-save', '');
  }

  /**
     * Keep the session open, restarting the countDown
     *
     * @param fromAjax - Flag to detect if the petition if made from the ajax interceptor.
     *                   If is true, we don't to indicate that we are session.
     */
  function keepSessionOpen(fromAjax) {
    // If this feature is not enabled do nothing
    if (!isSessionTimeoutEnabled) {
      return;
    }

    startCountDown();

    // If the call is NOT made from the ajax interceptor indicate that we want keep the session open.
    // Could be because the user indicates on the warn dialog that he stills there.
    if (!fromAjax) {
      keepAlive();
    }
  }

  /**
     * Expose the public methods
     * Revealing pattern
     */
  return {
    keepSessionOpen: keepSessionOpen };

};

bizagi.injector.register('sessionTimeOutService', ['dataService', 'globalHandlersService', bizagi.workportal.services.sessionTimeOut]);