bizagi.workportal.services.service.extend("bizagi.workportal.services.service", {}, {
  /*DISCUSSION*/
  getDiscussionsData: function getDiscussionsData(dataForm) {
    var self = this;
    var url = self.serviceLocator.getUrl("project-resource-get");

    return $.read({
      batchRequest: true,
      url: url,
      data: dataForm,
      dataType: "json" }).
    pipe(function (response) {
      return response;
    });
  },

  postDiscussion: function postDiscussion(data) {
    var self = this;
    var url = self.serviceLocator.getUrl("project-resource-action");
    return $.create({
      url: url.replace("{typeResource}", "DiscussionGuid"),
      data: JSON.stringify(data),
      dataType: "json",
      contentType: "application/json" });

  },

  postTag: function postTag(data) {
    var self = this;
    var url = self.serviceLocator.getUrl("project-resource-action");
    return $.create({
      url: url.replace("{typeResource}", "Tag"),
      data: JSON.stringify(data),
      dataType: "json",
      contentType: "application/json" });

  },

  deleteTag: function deleteTag(data) {
    var self = this;
    var url = self.serviceLocator.getUrl("project-resource-delete");
    url = url.replace("{typeResource}", "Tag");
    url = url.replace("{idResource}", data.idTag);
    return $.destroy({
      url: url,
      data: JSON.stringify(data),
      dataType: "json",
      contentType: "application/json" });

  },

  /*COMMENTS*/
  getCommentsData: function getCommentsData(data) {
    var self = this;
    var url = self.serviceLocator.getUrl("project-comments-get");

    return $.read({
      batchRequest: true,
      url: url,
      data: data,
      dataType: "json",
      contentType: "application/json" }).
    pipe(function (response) {
      return response;
    });
  },

  getCommentsCountByParent: function getCommentsCountByParent(idParent) {
    var self = this;

    var data = {};
    data.idParent = idParent;

    var urlRest = self.serviceLocator.getUrl("project-comments-count-by-parent");

    return $.read({
      url: urlRest,
      data: data,
      dataType: "json",
      contentType: "application/json" });

  },

  postComment: function postComment(data) {
    var self = this;
    var url = self.serviceLocator.getUrl("project-resource-action");

    return $.create({
      url: url.replace("{typeResource}", "CommentGuid"),
      data: JSON.stringify(data),
      dataType: "json",
      contentType: "application/json" });

  },

  deleteComment: function deleteComment(data) {
    var self = this;
    var url = self.serviceLocator.getUrl("project-resource-action");
    return $.destroy({
      url: url.replace("{typeResource}", "CommentGuid"),
      data: JSON.stringify(data),
      dataType: "json",
      contentType: "application/json" });

  },

  editDiscussion: function editDiscussion(data) {
    var self = this;
    var url = self.serviceLocator.getUrl("project-resource-action");
    return $.update({
      url: url.replace("{typeResource}", "DiscussionGuid"),
      data: JSON.stringify(data),
      dataType: "json",
      contentType: "application/json" });

  },

  //ATTACHMENTS
  getDownloadAttachment: function getDownloadAttachment(guidAttachment) {
    var self = this;
    var url = self.serviceLocator.getUrl("project-attachment-download");
    url = url.replace("{Attachment}", guidAttachment);

    $("#project-dashboard-download").prop("src", url);
  } });