/**
 *   Name: Bizagi Workportal Service Related Users
 *   Author: Elkin Fernando Siabato Cruz
 *
 *   Summary: This widget, show related users the tesks
 */
bizagi.workportal.services.relatedusers = function (dataService, loadTemplatesService) {
  var self = this;
  self.init = function () {
    self.auxNoRepeatTypesUser = {};
    self.dataRelatedUsers = [];

    self.globalHandlersService = bizagi.injector.get('globalHandlersService');
    self.usersAssignees = [];
  };
  self.init();
  self.dataService = dataService;
  self.idCase;
  self.content;
  self.hashTypesUser = {
    "assigned": bizagi.localization.getResource("workportal-project-user-assigned"),
    "owner": bizagi.localization.getResource("workportal-project-user-owner") };


  //load Templates
  loadTemplatesService.loadTemplates({
    "relatedUsersTemplate": bizagi.getTemplate("bizagi.workportal.services.relatedusers").concat("#relatedusers-wrapper") });


  /**
                                                                                                                               * Render all users with funcionality tooltip
                                                                                                                               */
  self.render = function (wrapper, selector, idCase, typesToAssignUsers) {
    self.init();
    self.$wrapper = wrapper;
    self.selector = selector;
    self.idCase = idCase;

    self.dataService.getCaseAssignees({ idCase: idCase }).done(function (dataAssignedUser) {
      dataAssignedUser.assignees = self.searchForUnassignedUserWorkitems(dataAssignedUser.assignees);
      var csvUserIds = self.getCSVUserIds(dataAssignedUser.assignees, typesToAssignUsers);
      $.when(self.getUsersData(csvUserIds)).done(function (rawData) {

        self.prepareData(rawData, typesToAssignUsers, dataAssignedUser);
        self.printUsers(self.dataRelatedUsers);
        self.addEventHandlersTooltip();
        self.addEventHandlersModal();
      });
    });
  };

  /**
      * Search for tasks with AssignmentMethodEveryOne and unassigned user conditions, 
      * and replace all users with unassigned user
      * @param assignees
      */
  self.searchForUnassignedUserWorkitems = function (assignees) {
    var unassignedUserWorkitems = {};
    var workItemsWithUnassignedUser = [];
    //count for worktItems with taskHasAssignmentMethodEveryone = true
    for (i = 0; i < assignees.length; i++) {
      var assignee = assignees[i];
      if (assignee.idWorkItem != undefined) {
        if (unassignedUserWorkitems[assignee.idWorkItem] == undefined) {
          unassignedUserWorkitems[assignee.idWorkItem] = { assignationCount: 1, taskAndUserInfo: null };
        } else
        {
          if (unassignedUserWorkitems[assignee.idWorkItem].assignationCount == 1) {
            workItemsWithUnassignedUser.push(assignee.idWorkItem);
          }
          unassignedUserWorkitems[assignee.idWorkItem].assignationCount++;
        }
      }
    };

    //Return taskUserInfo for tasks with "unassigned" users
    unassignedUserWorkitems = $.map(unassignedUserWorkitems, function (task) {
      return task.assignationCount > 1 ? task.taskAndUserInfo : null;
    });

    assignees = $.map(assignees, function (assignee) {
      //remove user if task has unassigned
      if (workItemsWithUnassignedUser.indexOf(assignee.idWorkItem) >= 0) {
        return null;
      }
      return assignee;
    }).
    concat(unassignedUserWorkitems);

    return assignees;
  };

  /**
      *
      * @param assignees
      * @param typesToAssignUsers
      * @returns {*}
      */
  self.getCSVUserIds = function (assignees, typesToAssignUsers) {
    var arrayIdsUsers = $.map(assignees, function (itemUser) {
      return itemUser.idUser;
    });
    if (typesToAssignUsers.length > 0 && typesToAssignUsers[0].id != undefined) arrayIdsUsers.unshift(typesToAssignUsers[0].id.toString());
    arrayIdsUsers = arrayIdsUsers.filter(function (item, pos, self) {
      return self.indexOf(item) == pos;
    });
    return arrayIdsUsers.join(",");
  };

  /**
      *
      * @param assignees
      * @param typesToAssignUsers
      * @returns {*}
      */
  self.getCSVUserGuids = function (assignees, typesToAssignUsers) {
    var arrayIdsUsers = $.map(assignees, function (itemUser) {
      return itemUser.guidUser;
    });
    return arrayIdsUsers.join(",");
  };

  /**
      * Get data users from idWorkItem with service
      */
  self.getUsersData = function (csvUserIds) {
    var self = this;
    var defer = $.Deferred();
    var params = {
      userIds: csvUserIds,
      width: 50,
      height: 50 };


    self.dataService.getUsersData(params).done(function (response) {
      defer.resolve(response);
    }).fail(function () {
      defer.resolve([]);
    });

    return defer.promise();
  };

  /**
      * Organize users data for render template
      */
  self.prepareData = function (rawUsersData, typesToAssignUsers, dataAssignedUser) {
    var dataProcessed = [];
    if (rawUsersData) {
      self.mergeTypesToAssignUsers(typesToAssignUsers, dataAssignedUser);
      for (var i = 0, j = rawUsersData.length; i < j; i++) {
        var user = self.prepareDataUser(rawUsersData[i], typesToAssignUsers, dataAssignedUser);
        dataProcessed.push(user);
        self.usersAssignees.push(self.getUsersAssignees(user));
      }
    }
    self.dataRelatedUsers = dataProcessed;
  };


  /**
      *
      * @param typesToAssignUsers
      * @param dataAssignedUser
      */
  self.mergeTypesToAssignUsers = function (typesToAssignUsers, dataAssignedUser) {
    for (var i = 0, totalDataAssigned = dataAssignedUser.assignees.length; i < totalDataAssigned; i++) {
      //Using the hash, validate no repeat elements on typesToAssignUsers
      var temp = self.auxNoRepeatTypesUser[dataAssignedUser.assignees[i].idUser.toString() + "assigned"];
      if (typeof temp === "undefined") {
        self.auxNoRepeatTypesUser[dataAssignedUser.assignees[i].idUser.toString() + "assigned"] = "";
        typesToAssignUsers.push({
          id: dataAssignedUser.assignees[i].idUser || -1,
          typeName: "assigned" });

      }
    }
    self.auxNoRepeatTypesUser = [];
  };

  /**
      * Render template users
      */
  self.printUsers = function () {
    self.content = loadTemplatesService.getTemplate("relatedUsersTemplate").render({ users: self.justAssignees(self.dataRelatedUsers) });

    $("span, li", self.content).removeClass("nodisplay");
    $(self.selector, self.$wrapper).html(self.content);
  };

  /**
      * return standar userAdapter information with only assigned users
      * @param {Array} allUsers 
      */
  self.justAssignees = function (allUsers) {
    var usersAssignee = [];
    allUsers.map(function (user) {
      var isOwner = user.types.indexOf("owner") !== -1;
      var isAssignee = user.types.indexOf("assigned") !== -1;
      usersAssignee.push(usersAdapter.createJsonUserInfo(user.id, user.name, user.username, user.picture, user.email, user.initials, isAssignee, isOwner, [], []));
    });
    return usersAdapter.justAssignees(usersAssignee);
  };

  /**
      * Prepare data by user
      */
  self.prepareDataUser = function (rawDataUser, typesToAssignUsers, dataAssignedUsers) {
    var id = rawDataUser.id;

    var user = {
      id: id,
      name: rawDataUser.name,
      email: rawDataUser.email,
      username: rawDataUser.username,
      initials: rawDataUser.name.getInitials(),
      picture: rawDataUser.picture ? "data:image/png;base64," + rawDataUser.picture : undefined,
      types: self.assignUserTypes(id.toString(), typesToAssignUsers),
      tasks: self.getTasksUser(dataAssignedUsers, id.toString()) };


    user.typesString = $.map(user.types, function (type) {
      return self.hashTypesUser[type];
    }).join(",");

    return user;
  };

  self.getUsersAssignees = function (user) {
    var self = this;
    var activities = [];
    var events = [];
    for (var i = 0; i < user.tasks.length; i++) {
      if (user.tasks[i].type === "event") {
        events.push({ "name": user.tasks[i].taskName });
      }
      if (user.tasks[i].type === "activity") {
        activities.push({ "name": user.tasks[i].taskName });
      }

    }

    var userAssignees = usersAdapter.createJsonUserInfo(
    user.id,
    user.name,
    user.username,
    user.picture,
    user.email,
    user.initials,
    self.getIsAssignee(user),
    self.getIsOwner(user),
    activities,
    events);

    return userAssignees;
  };

  /**
      *
      * @param dataAssignedUsers
      * @param idUser
      * @returns {Array}
      */
  self.getTasksUser = function (dataAssignedUsers, idUser) {
    var tasks = [];
    var dataAssignedUser = dataAssignedUsers.assignees.filter(function (user) {
      return user.idUser.toString() === idUser;
    });

    for (var i = 0, total = dataAssignedUser.length; i < total; i += 1) {
      tasks.push({
        taskType: bizagi.util.parseBoolean(dataAssignedUser[i].isEvent) ? bizagi.localization.getResource("workportal-project-user-event") : bizagi.localization.getResource("workportal-project-user-task"),
        taskName: dataAssignedUser[i].taskName,
        type: bizagi.util.parseBoolean(dataAssignedUser[i].isEvent) ? "event" : "activity" });

    }
    return tasks;
  };

  /**
      *
      * @param userId
      * @param typesToAssignUsers
      * @returns {Array}
      */
  self.assignUserTypes = function (userId, typesToAssignUsers) {
    var typesUser = [];

    for (var i = 0, totalTypeUser = typesToAssignUsers.length; i < totalTypeUser; i += 1) {
      if (userId === typesToAssignUsers[i].id.toString()) {
        typesUser.push(typesToAssignUsers[i].typeName);
      }
    }

    return typesUser;
  };

  /**
      * Add event handler for tooltip
      */
  self.addEventHandlersTooltip = function () {
    var self = this;
    $("ul.ui-bizagi-wp-userlist > li", self.content).not('.moreUsers').on('mouseenter', function (ev) {
      var $target = $(ev.target);
      var userId = $(this).data("userid");
      var userSelected = self.dataRelatedUsers.filter(function (user) {
        return user.id === userId;
      })[0];

      var userParam = usersAdapter.createJsonUserInfo(userSelected.id, userSelected.name, userSelected.username, userSelected.picture, userSelected.email, userSelected.initials, self.getIsAssignee(userSelected), self.getIsOwner(userSelected), self.getActivities(userSelected), self.getEvents(userSelected));

      $target.parent().usertooltip(loadTemplatesService, { target: $target, user: userParam });
    });

  };

  self.addEventHandlersModal = function () {
    $(".moreUsers").click(function (event) {
      event.preventDefault();
      event.stopPropagation();
      var usersAssignees = self.usersAssignees || [];
      self.globalHandlersService.publish("showDialogWidget", {
        widgetName: bizagi.workportal.widgets.widget.BIZAGI_WORKPORTAL_WIDGET_USERS_MODAL,
        closeVisible: true,
        data: usersAssignees,
        maximize: true,
        modalParameters: {
          title: bizagi.localization.getResource("workportal-project-users-title") + " (" + usersAssignees.length + ")",
          width: 790,
          height: 526,
          refreshInbox: false } });


    });
  };

  self.getActivities = function (userSelected) {
    var activities = [];
    for (var i = 0; i < userSelected.tasks.length; i++) {
      if (userSelected.tasks[i].type === "activity") {
        activities.push(userSelected.tasks[i].taskName);
      }
    }
    return activities;
  };

  self.getEvents = function (userSelected) {
    var events = [];
    for (var i = 0; i < userSelected.tasks.length; i++) {
      if (userSelected.tasks[i].type === "event") {
        events.push(userSelected.tasks[i].taskName);
      }
    }
    return events;

  };

  self.getIsAssignee = function (userSelected) {
    if (userSelected.types.indexOf("assigned") > -1) {
      return true;
    }
    return false;
  };

  self.getIsOwner = function (userSelected) {
    if (userSelected.types.indexOf("owner") > -1) {
      return true;
    }
    return false;
  };
};


bizagi.injector.register("relatedusers", ["dataService", 'loadTemplatesService', bizagi.workportal.services.relatedusers], true);