bizagi.workportal.services.behaviors = bizagi.workportal.services.behaviors || {};
bizagi.workportal.services.behaviors.plan = function (dataService) {
  var self = this;
  self.dataService = dataService;

  self.init = function () {

  };

  /**
      * adapter for standard planParent params
      */
  self.getPlanAsParent = function (plan) {
    var res = {};
    if (plan !== undefined && plan.id !== undefined) {
      res = {
        id: bizagi.clone(plan.id),
        idActivitySelected: plan.idActivitySelected };

    }
    return res;
  };

  return self;
};

bizagi.injector.register("bizagi.workportal.services.behaviors.plan", ["dataService", bizagi.workportal.services.behaviors.plan], true);